.continerCounter{
    height: 35vh;
 position: relative;
 font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.Lajas{
    width: 100%;
    height: auto;
    opacity: 0.95;
    
}


.Counter--text{
    color: rgb(0, 0, 0);
    position: absolute;
    bottom: 8px;
    left: 16px;
    font-size: 14px;
